.quota {
    padding-top: 40px;
}

.quota .used {
    background-color: red;
}

.quota .free {
    background-color: green;
}

.quota .trash {
    background-color: gray;
}

.quota div {
    height:20px;
    display: inline-block;
}

.quota button {
    position: relative;
    top: -6px;
}


